
































































































































































































































































































































































































































































































































































import Vue from 'vue';
import iconConfig from '@/store/misc/icons';
import { acModule, TokenConfig, Badge } from '@/store/modules/acs';
import AppreciationCard from '../data/ac';
import { TOKEN, calculateBonus } from '../interfaces/iac';

enum WEEKDAYS {
  SUN = 0,
  MON = 1,
  TUE = 2,
  WED = 3,
  THU = 4,
  FRI = 5,
  SAT = 6
}

enum MONTHS {
  JAN = 0,
  FEB = 1,
  MAR = 2,
  APR = 3,
  MAY = 4,
  JUN = 5,
  JUL = 6,
  AUG = 7,
  SEP = 8,
  OKT = 9,
  NOV = 10,
  DEC = 11
}

interface Dataset {
  values: Array<number>;
  labels: Array<string>;
  maxValue: number;
}

export default Vue.extend({
  name: 'Tokens',
  data() {
    return {
      iconConfig,
      toggleReward: 0 as number,
      toggleAwesome: 0 as number,
      timeRemaining: '' as string,
      rewardDialog: false as boolean,
      awesomeDialog: false as boolean,
      streakDialog: false as boolean,
      loginDialog: false as boolean
    }
  },
  computed: {
    tokenConfig(): TokenConfig {
      return acModule.tokenConfig;
    },
    rewardTokenCount(): number {
      return acModule.rewardTokenCount;
    },
    awesomeTokenCount(): number {
      return acModule.awesomeTokenCount;
    },
    rewardBadge(): Badge {
      return acModule.rewardBadge;
    },
    awesomeBadge(): Badge {
      return acModule.awesomeBadge;
    },
    streakBadge(): Badge {
      return acModule.streakBadge;
    },
    loginBadge(): Badge {
      return acModule.loginBadge;
    },
    canStreak(): number {
      return acModule.canStreak;
    },
    currentStreak(): number {
      return acModule.currentStreak;
    },
    currentBonus(): number {
      return calculateBonus(this.currentStreak);
    },
    allACs(): Array<AppreciationCard> {
      return acModule.allACs;
    },
    lastWeekRewardData(): Dataset {
      const DAY = (1000 * 60 * 60 * 24);
      const now = Date.now();
      const values: Array<number> = [];
      const labels: Array<string> = [];
      const weekACs: Array<AppreciationCard> = this.allACs.filter(ac => ac.dateSent >= now - DAY * 7 && ac.token === TOKEN.REWARD);
      for (let i=0; i<7; i++) {
        const currentDay = new Date(now - i * DAY).getDay();
        const currentDate = new Date(now - i * DAY).getDate();
        const list: Array<AppreciationCard> = weekACs.filter(ac => new Date(ac.dateSent).getDate() === currentDate);
        values[6-i] = list.length * acModule.tokenConfig.REWARD_TOKENS_PER_CARD + list.map(ac => ac.bonus).reduce((a, b) => a + b, 0);
        labels[6-i] = WEEKDAYS[currentDay];
      }
      return {
        values: values,
        labels: labels,
        maxValue: values.reduce((a,b) => Math.max(a,b)),
        title: 'Reward Tokens By Weekday'
      } as Dataset;
    },
    lastWeekAwesomeData(): Dataset {
      const DAY = (1000 * 60 * 60 * 24);
      const now = Date.now();
      const values: Array<number> = [];
      const labels: Array<string> = [];
      const weekACs: Array<AppreciationCard> = this.allACs.filter(ac => ac.dateSent >= now - DAY * 7 && ac.token === TOKEN.AWESOME);
      for (let i=0; i<7; i++) {
        const currentDay = new Date(now - i * DAY).getDay();
        const currentDate = new Date(now - i * DAY).getDate();
        const list: Array<AppreciationCard> = weekACs.filter(ac => new Date(ac.dateSent).getDate() === currentDate);
        values[6-i] = list.length * acModule.tokenConfig.AWESOME_TOKENS_PER_CARD + list.map(ac => ac.bonus).reduce((a, b) => a + b, 0);
        labels[6-i] = WEEKDAYS[currentDay];
      }
      return {
        values: values,
        labels: labels,
        maxValue: values.reduce((a,b) => Math.max(a,b)),
        title: 'Awesome Tokens By Weekday'
      } as Dataset;
    },
    lastYearRewardData(): Dataset {
      const now = Date.now();
      const values: Array<number> = [];
      const labels: Array<string> = [];
      const month = new Date(now).getMonth() + 12;
      const year = new Date(now).getFullYear();
      for (let i=0; i<12; i++) {
        const currentMonth = (month - i) % 12;
        const currentYear = (month - i) < 12 ? year - 1 : year;
        const list: Array<AppreciationCard> = this.allACs.filter(ac => 
          new Date(ac.dateSent).getMonth() === currentMonth 
          && new Date(ac.dateSent).getFullYear() === currentYear
          && ac.token === TOKEN.REWARD
        );
        values[11-i] = list.length * acModule.tokenConfig.REWARD_TOKENS_PER_CARD + list.map(ac => ac.bonus).reduce((a, b) => a + b, 0);
        labels[11-i] = MONTHS[currentMonth];
      }
      return {
        values: values,
        labels: labels,
        maxValue: values.reduce((a,b) => Math.max(a,b)),
        title: 'Reward Tokens By Month'
      } as Dataset;
    },
    lastYearAwesomeData(): Dataset {
      const now = Date.now();
      const values: Array<number> = [];
      const labels: Array<string> = [];
      const month = new Date(now).getMonth() + 12;
      const year = new Date(now).getFullYear();
      for (let i=0; i<12; i++) {
        const currentMonth = (month - i) % 12;
        const currentYear = (month - i) < 12 ? year - 1 : year;
        const list: Array<AppreciationCard> = this.allACs.filter(ac => 
          new Date(ac.dateSent).getMonth() === currentMonth 
          && new Date(ac.dateSent).getFullYear() === currentYear
          && ac.token === TOKEN.AWESOME
        );
        values[11-i] = list.length * acModule.tokenConfig.AWESOME_TOKENS_PER_CARD + list.map(ac => ac.bonus).reduce((a, b) => a + b, 0);
        labels[11-i] = MONTHS[currentMonth];
      }
      return {
        values: values,
        labels: labels,
        maxValue: values.reduce((a,b) => Math.max(a,b)),
        title: 'Awesome Tokens By Month'
      } as Dataset;
    },
    rewardData(): Array<Dataset> {
      return [this.lastWeekRewardData, this.lastYearRewardData]
    },
    awesomeData(): Array<Dataset> {
      return [this.lastWeekAwesomeData, this.lastYearAwesomeData]
    }
  },
  methods: {
    avatarColor(badge: Badge): string {
      return badge.level >= badge.levelMax ? badge.color : '';
    },
    timeRemaingString(): string {
      if (!this.canStreak) return '';
      const now: Date = new Date();
      return this.timeRemaining = `Next bonus in ${24 - now.getHours()}:${('0' + (60 - now.getMinutes())).slice(-2)}:${('0' + (60 - now.getSeconds())).slice(-2)}`
    }
  },
  created: function() {
    this.timeRemaingString();
    setInterval(() => {
      this.timeRemaingString();
    }, 1000);
  }
});
